import wPicker from "@/components/w-picker/w-picker.vue";

export default {
	components:{
		wPicker
	},
	data() {
		return {
			defaultVal:[10,0,5],
			address_id:'',
			consignee:'',
			mobile:'',
			province:'',
			province1:'',
			city:'',
			city1:'',
			district:'',
			district1:'',
			pcd:'',
			address:'',
			token:'',
			channel:1,
			is_default:false,
		}
	},
	created(){
		let _this=this;
		uni.getStorage({
			key: 'token',
			success: function (res) {
				_this.token=res.data;
			}
		});
	},
	mounted(){
		let _this=this;
	},	
	methods: {
		shouAddress:function(){
			let _this=this;
			_this.$refs.picker.show();
		},
		onConfirm:function(val){
			let _this=this;
			console.log(JSON.stringify(val))
			_this.pcd=val.result;
			_this.province=val.checkArr[0];
			_this.province1=val.defaultVal[0];
			_this.city=val.checkArr[1];
			_this.city1=val.defaultVal[1];
			_this.district=val.checkArr[2];
			_this.district1=val.defaultVal[2];
		},
		isDefault:function(){
			let _this=this;
			if(_this.is_default){
				_this.is_default=false;
				return
			}else{
				_this.is_default=true;
				return
			}
		},
		save:function(){
			let _this=this;
			let data={
				channel:_this.channel,
				token:_this.token,
				consignee:_this.consignee,
				province:_this.province,
				city:_this.city,
				district:_this.district,
				address:_this.address,
				mobile:_this.mobile,
				is_default:_this.is_default
			};
			_this.$axios(_this.$baseUrl.addAddress,data).then(res =>{
				if(res.data.status==1){
					uni.showToast({
						title: res.data.msg,
						icon:'success',
						duration: 1000
					});
					setTimeout(function(){
						uni.navigateBack({
							delta: 1
						});
						//_this.$store.commit('resturantName', true);
					},1000)
				}
			}).catch(error =>{
				
			})
		}
	}
}
